import React, { Component } from 'react';
import Menu from './Menu';
import bread from './bread.json';
import './App.scss';

class App extends Component {
  render() {
    const breadList = bread;

    return (
      <div className="MenuList">
          <h1 >이디아제과</h1>
          <ul className="cf">
              {
                bread.map( (data, index) => <Menu key={index} picture={data.image} name={data.name} /> )  
              }
          </ul>
      </div>
    );
  }
}

export default App;